import Vue from 'vue'
import Router from 'vue-router'
import WebIDE from '@/components/WebIDE'
import Monaco from '@/components/Monaco'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/cm',
      name: 'WebIDE',
      component: WebIDE,
    },
    {
      path: '/',
      name: 'Monaco',
      component: Monaco,
    }
  ]
})



